import React from "react";
import c from './Bottom.module.css';
import {NavLink} from "react-router-dom";

const Bottom = (props) => {
    return (<div className={c.bottom}>

        <div className={c.links}>
            <div><NavLink to='/contacts' className={c.contacts}></NavLink></div>
            <div><NavLink to='/guarantee' className={c.contacts}></NavLink></div>
            <div><NavLink to='/delivery' className={c.contacts}></NavLink></div>
        </div>

        <div className={c.info}>
            <h3>  : </h3>
            <p>  - : 10:00- 20:00  </p>
            <p> : 12:00 - 18:00  </p>
        </div>

        <div className={c.info}>2015 - 2020  </div>
    </div>);
}

export default Bottom;