const filterArray= (array, data)=>{
    let newArray= array;
    let i=0;
    data.lprice ? newArray= newArray.filter(el=> el.price >= +data.lprice): i++;
    data.hprice ?  newArray= newArray.filter(el=> el.price <= +data.hprice): i++;
    data.memory ?  newArray= newArray.filter(el=> el.memory === +data.memory): i++;
    data.size ?  newArray= newArray.filter(el=> el.size === +data.size): i++;
return newArray;
}


export const dataReducer = (state = initionalState, action) => {
    switch (action.type) {
        case SETCURRENTSECTION:
            return {
                ...state,
                currentSection: [...state[action.section]],
                currentSectionCopy: [...state[action.section]],
                inputsData:{lprice: '', hprice: '',memory: '', size: ''}
            }
        case SETCURRENTITEM:
            return {
                ...state,
                currentItem: [...state[action.section]].find(p => p.model === action.model)
            }
        case CHANGEQUANTITY:
            return {
                ...state,
                [action.section]: state[action.section].map(
                    (p) => {
                        if (action.model === p.model) {
                            return {...p, quantity: p.quantity - action.amount}
                        }
                        return p;
                    }
                )
            }
        case SORT:
            return {
                ...state,
                currentSection: [...state.currentSection].sort((a, b) => action.order * (a[action.value] > b[action.value]
                    ? 1 : -1))
            }
        case SEARCH:
            return {
                ...state,
                searchResults: [...state.iphone, ...state.ipad, ...state.watch, ...state.mac, ...state.accestories].filter(p => p.model.toLowerCase().includes(action.value.toLowerCase()))
            }

        case FILTER:
            return {
                ...state,
                currentSection:  filterArray([...state.currentSectionCopy],  state.inputsData)
            }
        case ADDINPUTDATA:
            return {
                ...state,
                inputsData: {...state.inputsData,  [action.name]: action.value}
            }

        default:
            return state;
    }
}

export const setCurrentItem = (section, model) => {
    return {type: SETCURRENTITEM, section, model}
}

export const setCurrentSection = (section) => {
    return {type: SETCURRENTSECTION, section}
}

export const changeQuantity = (model, section, amount) => {
    return {type: CHANGEQUANTITY, model, section, amount}
}

export const sort = (value, order) => {
    return {type: SORT, value, order}
}

export const search = (value) => {
    return {type: SEARCH, value}
}

export const addInputData = ( name, value) => {
    return {type: ADDINPUTDATA,  name, value }
}

export const filter = () => {
    return {type: FILTER }
}